
import { mapState } from "vuex";
import { MessageBox, Toast, InfiniteScroll } from "mint-ui";
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import vNodata from "@/components/v-nodata";
import vHeader from "@/components/v-header";
import vFooter from "@/components/v-footer";
const qs = require("qs");
export default {
data() {
return {
totalPage: 1,
currentPage: 1,
urlPic:this.api.urlPic,
show: false,
storeData: '',
pullupEnabled: true,
statusLoad:{
pullupStatus: 'default'
},
status: this.getUrlParam("status"),
choseDex: this.getUrlParam("choseDex")||null,
orderNav: [
{
title: "待付款",
status: 10
},
{
title: "待接单",
status: 20
},
{
title: "待发货",
status: 30
},
{
title: "待收货",
status: 40
},
{
title: "待评价",
status: 50
}
],
orderList: [],
ordersPageNo: 1,
ordersPageSize: 10,
moreOrders: false,
noOrders: false,
loadMoreVal:true,
loading: true
};
},
components: {
"v-nodata": vNodata,
vHeader,
"v-footer": vFooter,
Scroller,
Spinner
},
computed: {
...mapState(["token"])
},
beforeCreate() {
document.title = "我的订单";
},
created() {
this.verToken();

this.clearData();
this.getOrdersList();
},
methods: {
goMap(title,item){
this.$router.push({//核心语句
path:'/map',//跳转的路径
query:{//路由传参时push和query搭配使用 ，作用时传递参数
title:title,
latitude: item.latitude,
longitude: item.longitude,
address: item.merchantAddress,
merchantName: item.merchantName
}
})
},
callService(phone){
window.location.href = 'tel:'+phone;
},
toastMap(shoreId){
this.storeData = '';
this.$axios.get(this.api.sellerstoreData+shoreId)
.then(res => {
if(res.data.code === 1){
let arrData = res.data.content;
this.storeData = arrData;
this.show = true;
}
})
.catch(res => {
this.showTip("网络错误");
});
},
closePickerBox(){
this.show = false;
},
verToken(){
if(!this.token){
this.showTip("登录超时，请重新登录");
this.$router.push({path: "/mine/login"});
}
},
pageToDtail(orderNo,status){
this.$router.push('/store/orderDetail?orderNo='+orderNo+'&status='+status);
},

clearData() {
this.currentPage = 1;
this.totalPage = 1;
this.noOrders = false;
this.loading = true;
this.orderList = [];
},

getStatusTxt(choseDex) {
let text = "";
switch (choseDex) {
case 10:
text = "下单未支付";
break;
case 20:
text = "下单已支付";
break;
case 30:
text = "商户接单";
break;
case 40:
text = "配送订单";
break;
case 50:
text = "配送完成";
break;
case 60:
text = "订单已完成";
break;
}
return text;
},

getArrImg(arr) {
if (!arr) {
return [];
}
const arrImg = [];
arr.forEach(val => {
val.goodsPhoto = this.api.urlPic + val.goodsPhoto.split(',')[0];
});
return arr;
},

tabOrderStatus(item, index) {
if (this.choseDex !== index) {
this.orderList = [];
this.totalPage=1;
this.currentPage=1;
this.choseDex = index;
this.clearData();
this.status = item.status;
this.getOrdersList();

setTimeout(()=>{
if(this.orderList){
this.$refs.scroller.$el.children[0].style.cssText='transform-origin: 0px 0px 0px; transform: translateX(0px) translateY(0px) translateZ(0px) scale(1, 1); transition: none 0s ease 0s;';
}
},0)

}
},

loadMore() {
if(this.loadMoreVal){
this.loadMoreVal=false;
setTimeout(()=>{
this.loadMoreVal=true;
},1000);
this.loading = true;
this.ordersPageNo += 1;
this.getOrdersList();
console.log(' 加载');
}else{
return;
}
},

getOrdersList(first) {
let obj={};
if(this.status){
obj={
status: this.status,
page: this.currentPage,
limit: 8
}
}else{
obj={
page: this.currentPage,
limit: 8
}
}
if(this.currentPage<=this.totalPage){
this.$axios
.post(this.api.otoOrderformList,
JSON.stringify(obj),
{
headers: {
"content-type": "application/json",
"Authorization": this.token
}
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
if (first) {
this.noOrders = true;
} else {
this.moreOrders = true;
this.loading = false;
}
return;
}
let objData = resData.content,
arrData = objData.list;

this.currentPage = objData.currPage+1;
this.totalPage = objData.totalPage;

if (arrData.length === 0) {
this.orderList = null;
this.noOrders = true;
this.loading = true;
return;
}

arrData.forEach(val => {
let obj = {
date: val.addTime,
num: this.goodsCount(val.items),
status: this.getStatusTxt(val.status),
statusNum: val.status,
storeName: val.storeName,
sendType: val.sendType,
storeLogo: this.urlPic+val.storeLogo,
storePhone: val.storePhone,
storeId: val.storeId,
totalPrice: val.totalPrice,
deductedPrice: val.deductedPrice||null,
conList: this.getArrImg(val.items),
orderNo: val.orderNumber
};

/*if (this.choseDex == 40) {
val.orderDetails.forEach(vl => {
obj.arrGood.push({
id: vl.productId,
detailId: vl.id,
name: vl.productName,
img: vl.product.photoMainUrls[0],
num: vl.productCount,
price: vl.productSalePrice,
weight: vl.product.weight,
date: vl.createDate
});
});
}*/
this.orderList.push(obj);
});
if (pageCount <= this.ordersPageNo) {
this.loading = true;
this.moreOrders = false;
} else {
this.loading = false;
this.moreOrders = true;
}
})
.catch(res => {
if (first) {
this.noOrders = true;
} else {
this.loading = false;
}
});
}else{
this.statusLoad.pullupStatus = 'complete';
setTimeout(()=>{
this.statusLoad.pullupStatus = 'default';
},1000);
console.log('已加载完');
}
},
goodsCount(items){
let count = 0;
for( let i of items){
count += i.goodsCount;
}
return count
},

pageToCenter(type, index) {
const orderList = this.orderList;
let objType = {};
switch (type) {
case "exp":
objType.tip = "没有查询到物流信息";
objType.key = "expList";
objType.url = "/store/evaStore?from=orders&type=exp";
break;
case "eva":
objType.tip = "没有查询到可评价的商品";
objType.key = "evaList";
objType.url = "/store/evaStore?from=orders&type=eva";
break;
}

if (orderList.length == 0) {
this.showTip(objType.tip);
return;
}

sessionStorage.setItem(objType.key, JSON.stringify(orderList[index]));
console.log(orderList[index]);
setTimeout(() => {
this.$router.push(objType.url);
}, 0);
},

receiptOrder(orderNo, index) {
MessageBox({
title: "收货提示",
message: "您确定收到货品了吗？",
showCancelButton: true
}).then(action => {
if (action === "confirm") {
this.$axios
.get(this.api.receptGoods+orderNo,{
headers: { "Authorization": this.token }
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.showTip(resData.message);
return;
}
this.showTip("确认收货成功！", 2000, "ok");
this.orderList.splice(index, 1);

if (this.orderList.length === 0) {
this.ordersPageNo += 1;
this.getOrdersList();
}
})
.catch(res => {
this.showTip("确认收货失败，请稍后重试");
});
}
});
},

cancelOrder(item) {
MessageBox({
title: "取消提示",
message: "您确定要取消此订单吗？",
showCancelButton: true
}).then(action => {
if (action === "confirm") {
this.$axios
.get(this.api.otoCancelOrder+item.orderNo, {
headers: { "Authorization": this.token }
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.showTip(resData.msg);
return;
}
item.statusNum=0;

this.showTip("取消订单成功");
})
.catch(res => {
this.showTip("取消失败，请稍后重试");
});
}
});
},

onPayOrder(orderNo, index) {

const loading = Toast({
message: "支付中...",
iconClass: "loading",
duration: 30000
});
this.$axios
.post(
this.api.payOrder,
JSON.stringify({
'channel': 2,
'orderNumbers': orderNo,
'payType': 'wxpublicpay'
}),
{
headers: {
"content-type": "application/json",
"Authorization": this.token
}
}
)
.then(res => {
loading.close();
const resData = res.data;
if (resData.code !== 1) {
this.showTip("支付失败，请稍后重试");
return;
}

const objData = resData.content;
const weiXinArg = {
appId: objData.appid,
timeStamp: objData.timestamp,
nonceStr: objData.nonceStr,
package: objData.packages,
signType: "MD5",
paySign: objData.paySign
};
this.weiXinPay(weiXinArg);
})
.catch(res => {
loading.close();
this.showTip("支付失败，请稍后重试");
});
},

weiXinPay(weiXinArg) {
let vue = this;

function onBridgeReady() {
WeixinJSBridge.invoke("getBrandWCPayRequest", weiXinArg, res => {
if (res.err_msg == "get_brand_wcpay_request:ok") {

vue.showTip("支付成功", 2000, "ok");
vue.orderList.splice(index, 1);

if (vue.orderList.length === 0) {
vue.ordersPageNo += 1;
vue.getOrdersList();
}
} else if (res.err_msg == "get_brand_wcpay_request:cancel") {

vue.showTip("您已取消支付");
} else {

vue.showTip("支付失败，请重试");
}
});
}

if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener(
"WeixinJSBridgeReady",
onBridgeReady,
false
);
} else if (document.attachEvent) {
document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
}
} else {
onBridgeReady();
}
},

pageToPay(payPrice, orderNo, status) {

if (status === 10) {
let url = "/store/otoPay?orderNumbers=" + orderNo + "&payPrice=" + payPrice;
this.$router.push(url);
}else{
vue.showTip("跳转商品详情");
}
},

refundOrder(orderId) {
MessageBox({
title: "退款提示",
message: "目前还不支持退款，您可以拨打客服电话进行相关操作！",
showCancelButton: true
}).then(action => {
if (action === "confirm") {
this.callService();
}
});
},

callHelp() {
this.callService();
}
}
};



